A,CACA,gBAGA,CACA,2CACA,kBAAA,CACA,oBAAA,CAYA,yBAAA,CACA,UAAA,CAXA,oBAAA,CAEA,mBAAA,CAGA,mBAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CALA,YAAA,CAQA,iBAAA,CAVA,UAaA,CACA,2BAEA,WAAA,CADA,SAEA,CACA,6BACA,YAAA,CACA,gBACA,CACA,mCASA,aAAA,CADA,YAAA,CAPA,mBAAA,CAGA,cAAA,CAFA,iBAAA,CACA,eAAA,CAEA,gBAAA,CAEA,UAGA,CACA,qDAEA,kBAAA,CADA,YAEA,CAEA,8DACA,uBACA,CAEA,yCAIA,4HACA,YACA,CACA,qCAEA,cAAA,CADA,6BAEA,CACA,yCACA,SACA,CACA,wCAEA,WAAA,CADA,UAEA,CACA,iEACA,aACA,CAIA,qIACA,YACA,CACA,sEACA,aACA,CACA,2BACA,UACA,CACA,6CACA,OACA,CACA","sourcesContent":["<template>\r\n    <div class=\"wed-host-section\">\r\n        <div class=\"wed-host-section-container\">\r\n            <div class=\"support-memebers-title\">\r\n                <span>Support Members</span>\r\n                <img\r\n                    v-if=\"!editing\"\r\n                    @click=\"doEdit(true)\"\r\n                    src=\"/assets/user-plus.svg\"\r\n                    alt=\"\"\r\n                />\r\n            </div>\r\n            <div class=\"d-flex justify-content-around my-4\">\r\n                <button\r\n                    v-if=\"editing && members.length > 0\"\r\n                    @click=\"doEdit(false)\"\r\n                    class=\"btn btn-sm btn-cancel\"\r\n                    title=\"close form\"\r\n                >\r\n                    Close\r\n                </button>\r\n                <!-- <button\r\n                    v-else\r\n                    @click=\"doEdit(true)\"\r\n                    class=\"btn btn-sm wed-btn-main\"\r\n                    title=\"add new Member\"\r\n                >\r\n                    <span class=\"material-icons\"> person_add </span>\r\n                </button> -->\r\n\r\n                <!-- <button\r\n                            class=\"btn btn-sm wed-btn-main\"\r\n                            title=\"addMembers in bulk\"\r\n                        >\r\n                            <span class=\"material-icons\">\r\n                                group_add\r\n                            </span>\r\n                            </button> -->\r\n            </div>\r\n\r\n            <div class=\"wed-host-section-container-form\" v-if=\"editing\">\r\n                <form class=\"form-row\">\r\n                    <div\r\n                        class=\"alert alert-danger\"\r\n                        role=\"alert\"\r\n                        v-if=\"hasError\"\r\n                    >\r\n                        {{ errMessage }}\r\n                    </div>\r\n                    <div class=\"form-group-custom\">\r\n                        <label for=\"name\" class=\"form-label\">Name</label>\r\n                        <input\r\n                            type=\"text\"\r\n                            class=\"form-control\"\r\n                            id=\"name\"\r\n                            required\r\n                            placeholder=\"Name\"\r\n                            name=\"name\"\r\n                            v-model=\"newMember.name\"\r\n                        />\r\n                        <span v-if=\"errors && errors.name\" class=\"errMsg\">{{\r\n                            errors.name[0]\r\n                        }}</span>\r\n                    </div>\r\n                    <div class=\"form-group-custom\">\r\n                        <label for=\"email\" class=\"form-label\">Email</label>\r\n                        <input\r\n                            type=\"email\"\r\n                            class=\"form-control\"\r\n                            id=\"email\"\r\n                            required\r\n                            placeholder=\"Email\"\r\n                            name=\"email\"\r\n                            v-model=\"newMember.email\"\r\n                        />\r\n                        <span v-if=\"errors && errors.email\" class=\"errMsg\">{{\r\n                            errors.email[0]\r\n                        }}</span>\r\n                    </div>\r\n                    <div class=\"form-group-custom\">\r\n                        <label for=\"mobile\" class=\"form-label\">Mobile</label>\r\n                        <input\r\n                            type=\"number\"\r\n                            class=\"form-control\"\r\n                            id=\"mobile\"\r\n                            required\r\n                            placeholder=\"Mobile\"\r\n                            name=\"mobile\"\r\n                            v-model=\"newMember.mobile\"\r\n                            oninput=\"javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);\"\r\n                            maxlength=\"10\"\r\n                            @keydown=\"numberRistrict()\"\r\n                        />\r\n                        <span v-if=\"errors && errors.mobile\" class=\"errMsg\">{{\r\n                            errors.mobile[0]\r\n                        }}</span>\r\n                    </div>\r\n                    <div\r\n                        v-if=\"showForm == false\"\r\n                        class=\"form-group-custom text-center\"\r\n                    >\r\n                        <button\r\n                            type=\"submit\"\r\n                            class=\"btn btn-sm\"\r\n                            @click.prevent=\"createMember()\"\r\n                        >\r\n                            <span>Submit</span>\r\n                        </button>\r\n                    </div>\r\n                    <div\r\n                        v-if=\"showForm == true\"\r\n                        class=\"form-group-custom text-center\"\r\n                    >\r\n                        <button\r\n                            type=\"submit\"\r\n                            class=\"btn btn-sm\"\r\n                            @click.prevent=\"updateMember()\"\r\n                        >\r\n                            <span>Update</span>\r\n                        </button>\r\n                    </div>\r\n                </form>\r\n            </div>\r\n\r\n            <div class=\"table-responsive\" v-if=\"members.length > 0\">\r\n                <div\r\n                    v-for=\"member in reversedMembers\"\r\n                    :key=\"member.id\"\r\n                    class=\"member-list-single\"\r\n                    @click=\"toggle($event)\"\r\n                >\r\n                    <div class=\"list-single-name\">{{ member.name }}</div>\r\n                    <div class=\"list-single-email\">{{ member.email }}</div>\r\n                    <div class=\"list-single-email\">\r\n                        <div>{{ member.mobile }}</div>\r\n                        <div v-if=\"!member.roles\">\r\n                            <img\r\n                                @click.stop=\"\r\n                                    setVal(\r\n                                        member.id,\r\n                                        member.name,\r\n                                        member.email,\r\n                                        member.mobile\r\n                                    )\r\n                                \"\r\n                                src=\"/assets/edit.svg\"\r\n                                alt=\"\"\r\n                            />\r\n                            <img\r\n                                @click.stop=\"deleteMember(member)\"\r\n                                src=\"/assets/trash.svg\"\r\n                                alt=\"\"\r\n                                style=\"margin-right: 0 !important\"\r\n                            />\r\n                            <i class=\"fas fa-chevron-down drop-arrow\"></i>\r\n                            <i class=\"fas fa-chevron-up drop-arrow\"></i>\r\n                        </div>\r\n                    </div>\r\n                    <div class=\"list-single-drop\">\r\n                        <div>{{ member.email }}</div>\r\n                        <div>{{ member.mobile }}</div>\r\n                    </div>\r\n                </div>\r\n                <!-- <table class=\"table table-striped mx-auto w-auto\" id=\"table\">\r\n                    <thead>\r\n                        <tr>\r\n                            <th scope=\"col\">Name</th>\r\n                            <th scope=\"col\">Email</th>\r\n                            <th scope=\"col\">Mobile</th>\r\n                            <th scope=\"row\" colspan=\"2\" class=\"text-center\">\r\n                                Actions\r\n                            </th>\r\n                        </tr>\r\n                    </thead>\r\n                    <tbody>\r\n                        <tr v-for=\"member in reversedMembers\" :key=\"member.id\">\r\n                            <th scope=\"row\">{{ member.name }}</th>\r\n                            <td>{{ member.email }}</td>\r\n                            <td>{{ member.mobile }}</td>\r\n                            <td\r\n                                @click=\"\r\n                                    setVal(\r\n                                        member.id,\r\n                                        member.name,\r\n                                        member.email,\r\n                                        member.mobile\r\n                                    )\r\n                                \"\r\n                            >\r\n                                <span class=\"material-icons text-primary\"\r\n                                    >edit</span\r\n                                >\r\n                            </td>\r\n                            <td @click=\"deleteMember(member)\" class=\"text-danger\">\r\n                                <span class=\"material-icons text-danger\"\r\n                                    >delete</span\r\n                                >\r\n                            </td>\r\n                        </tr>\r\n                    </tbody>\r\n                </table> -->\r\n            </div>\r\n            <!-- <div v-if=\"showForm\" class=\"overlay-form\">\r\n                <form @submit.prevent=\"updateMember($event)\" class=\"task-form-emp\">\r\n                    <div class=\"d-flex justify-content-end\">\r\n                        <i\r\n                            @click=\"showForm = false\"\r\n                            style=\"font-size: 30px; cursor: pointer\"\r\n                            class=\"fas fa-times-circle\"\r\n                        ></i>\r\n                    </div>\r\n                    <div class=\"alert alert-danger\" role=\"alert\" v-if=\"hasError\">\r\n                        {{ errMessage }}\r\n                    </div>\r\n                    <div class=\"form-group\">\r\n                        <label for=\"name\" class=\"form-label\">Name</label>\r\n                        <input\r\n                            type=\"text\"\r\n                            class=\"form-control\"\r\n                            id=\"e_name\"\r\n                            name=\"name\"\r\n                            required\r\n                            :value=\"this.e_name\"\r\n                        />\r\n                        <span v-if=\"errors && errors.name\" class=\"errMsg\">{{\r\n                            errors.name[0]\r\n                        }}</span>\r\n                    </div>\r\n                    <div class=\"form-group\">\r\n                        <label for=\"email\" class=\"form-label\">Email</label>\r\n                        <input\r\n                            type=\"text\"\r\n                            class=\"form-control\"\r\n                            id=\"e_email\"\r\n                            name=\"email\"\r\n                            required\r\n                            :value=\"this.e_email\"\r\n                        />\r\n                        <span v-if=\"errors && errors.email\" class=\"errMsg\">{{\r\n                            errors.email[0]\r\n                        }}</span>\r\n                    </div>\r\n                    <div class=\"form-group\">\r\n                        <label for=\"mobile\" class=\"form-label\">Mobile</label>\r\n                        <input\r\n                            type=\"number\"\r\n                            class=\"form-control\"\r\n                            id=\"e_mobile\"\r\n                            name=\"mobile\"\r\n                            required\r\n                            :value=\"this.e_mobile\"\r\n                            oninput=\"javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);\"\r\n                            maxlength=\"10\"\r\n                            @keydown=\"numberRistrict()\"\r\n                        />\r\n                        <span v-if=\"errors && errors.mobile\" class=\"errMsg\">{{\r\n                            errors.mobile[0]\r\n                        }}</span>\r\n                    </div>\r\n                    <button type=\"submit\" class=\"btn btn-primary mb-2 btn-custm\">\r\n                        Save\r\n                    </button>\r\n                </form>\r\n            </div> -->\r\n            <!-- Modal -->\r\n            <div id=\"myModal\" class=\"modal fade\" role=\"dialog\">\r\n                <div class=\"modal-dialog\">\r\n                    <!-- Modal content-->\r\n                    <div class=\"modal-content\">\r\n                        <div class=\"modal-header\">\r\n                            <h4 class=\"modal-title\">Edit member</h4>\r\n                            <button\r\n                                type=\"button\"\r\n                                class=\"close\"\r\n                                data-dismiss=\"modal\"\r\n                            >\r\n                                &times;\r\n                            </button>\r\n                        </div>\r\n                        <div class=\"modal-body\">\r\n                            <input\r\n                                type=\"hidden\"\r\n                                disabled\r\n                                class=\"form-control\"\r\n                                id=\"e_id\"\r\n                                name=\"id\"\r\n                                required\r\n                                :value=\"this.e_id\"\r\n                            />\r\n                            Name:\r\n                            <input\r\n                                type=\"text\"\r\n                                class=\"form-control\"\r\n                                id=\"e_name\"\r\n                                name=\"name\"\r\n                                required\r\n                                :value=\"this.e_name\"\r\n                            />\r\n                            Email:\r\n                            <input\r\n                                type=\"text\"\r\n                                class=\"form-control\"\r\n                                id=\"e_email\"\r\n                                name=\"email\"\r\n                                required\r\n                                :value=\"this.e_email\"\r\n                            />\r\n                            Mobile:\r\n                            <input\r\n                                type=\"number\"\r\n                                class=\"form-control\"\r\n                                id=\"e_mobile\"\r\n                                name=\"mobile\"\r\n                                required\r\n                                :value=\"this.e_mobile\"\r\n                                oninput=\"javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);\"\r\n                                maxlength=\"10\"\r\n                                @keydown=\"numberRistrict()\"\r\n                            />\r\n                        </div>\r\n                        <div class=\"modal-footer\">\r\n                            <button\r\n                                type=\"button\"\r\n                                class=\"btn btn-sm wed-btn-main close\"\r\n                                @click=\"updateMember()\"\r\n                                data-dismiss=\"modal\"\r\n                            >\r\n                                <span class=\"material-icons\">save</span>\r\n                            </button>\r\n                        </div>\r\n                    </div>\r\n                </div>\r\n            </div>\r\n            <div\r\n                v-if=\"message\"\r\n                class=\"alert alert-success alert-dismissible fade show mt-3\"\r\n                role=\"alert\"\r\n            >\r\n                {{ message }}\r\n                <button\r\n                    type=\"button\"\r\n                    class=\"close\"\r\n                    data-dismiss=\"alert\"\r\n                    aria-label=\"Close\"\r\n                >\r\n                    <span aria-hidden=\"true\">&times;</span>\r\n                </button>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nexport default {\r\n    props: [\"hostId\"],\r\n    data() {\r\n        return {\r\n            editing: false,\r\n            newMember: {},\r\n            hasError: false,\r\n            errMessage: \"\",\r\n            members: [],\r\n            e_id: \"\",\r\n            e_name: \"\",\r\n            e_email: \"\",\r\n            e_mobile: \"\",\r\n            errors: null,\r\n            showForm: false,\r\n            message: null,\r\n            success: false,\r\n        };\r\n    },\r\n    mounted() {\r\n        this.$nextTick(function () {\r\n            this.getMembers();\r\n        });\r\n    },\r\n\r\n    methods: {\r\n        doEdit(editing) {\r\n            this.editing = editing;\r\n            this.errMessage = \"\";\r\n            this.hasError = false;\r\n            this.errors = null;\r\n            this.newMember = {};\r\n            this.e_id = null;\r\n            this.showForm = false;\r\n        },\r\n        createMember() {\r\n            var input = this.newMember;\r\n            let slug;\r\n            if (input.name == \"\") {\r\n                slug =\r\n                    input.name.toLowerCase() +\r\n                    \"-\" +\r\n                    Math.floor(Math.random() * 100 + 1);\r\n            }\r\n            const host_id = this.hostId;\r\n            var input = { ...input, host_id, slug };\r\n            var _this = this;\r\n            let exists = null;\r\n            this.members.forEach(function (item) {\r\n                if (\r\n                    input[\"name\"] == null ||\r\n                    input[\"email\"] == null ||\r\n                    input[\"mobile\"] == null\r\n                ) {\r\n                    //console.log(\"All Feilds are Reqiured\");\r\n                    _this.errMessage = \"All Feilds are Reqiured\";\r\n                    exists = 0;\r\n                    return;\r\n                }\r\n                if (item.email == input[\"email\"]) {\r\n                    //console.log(\"Email Address Exists\");\r\n                    _this.errMessage = \"Email Address Exists\";\r\n                    exists = 0;\r\n                    return;\r\n                }\r\n                if (item.mobile == input[\"mobile\"]) {\r\n                    //console.log(\"Mobile Number Exists\");\r\n                    _this.errMessage = \"Mobile Number Exists\";\r\n                    exists = 0;\r\n                    return;\r\n                }\r\n            });\r\n\r\n            //console.log(this.errMessage);\r\n            if (exists == 0) {\r\n                this.hasError = true;\r\n            } else {\r\n                axios\r\n                    .post(\"/storemember\", input)\r\n                    .then(function (response) {\r\n                        _this.newMember = {\r\n                            name: \"\",\r\n                            email: \"\",\r\n                            mobile: \"\",\r\n                            host_id: _this.hostId,\r\n                            slug: input.name,\r\n                        };\r\n                        _this.hasError = false;\r\n                        _this.getMembers();\r\n                        _this.editing = false;\r\n                        _this.message = \"added\";\r\n                    })\r\n                    .catch((error) => {\r\n                        _this.errors = error.response.data.errors;\r\n                        console.log(\"Insert: \" + error.data);\r\n                    });\r\n            }\r\n        },\r\n        getMembers() {\r\n            var _this = this;\r\n            axios\r\n                .get(\"/getmembers\")\r\n                .then(function (response) {\r\n                    _this.members = response.data;\r\n                    if (_this.members.length == 0) {\r\n                        //console.log(this.members.length);\r\n                        _this.doEdit(true);\r\n                    }\r\n                })\r\n                .catch((error) => {\r\n                    console.log(\"Get All: \" + error);\r\n                });\r\n        },\r\n        deleteMember(member) {\r\n            var _this = this;\r\n            axios\r\n                .post(\"/deletemembers/\" + member.id)\r\n                .then(function (response) {\r\n                    _this.getMembers();\r\n                })\r\n                .catch((error) => {\r\n                    console.log(\"Delete member: \" + error);\r\n                });\r\n        },\r\n        setVal(val_id, val_name, val_email, val_mobile) {\r\n            this.doEdit(true);\r\n            this.e_id = val_id;\r\n            this.newMember.name = val_name;\r\n            (this.newMember.email = val_email),\r\n                (this.newMember.mobile = val_mobile);\r\n            this.showForm = true;\r\n        },\r\n        updateMember() {\r\n            var _this = this;\r\n            _this.errors = null;\r\n            var id_val_1 = this.e_id;\r\n            var name_val_1 = this.newMember.name;\r\n            var email_val_1 = this.newMember.email;\r\n            var mobile_val_1 = this.newMember.mobile;\r\n            //var model = document.getElementById(\"myModal\").value;\r\n            // console.log(_this.members.find((x) => x.email === email_val_1));\r\n            // if (_this.members.find((x) => x.email === email_val_1)) {\r\n            //     _this.errors = { email: \"email already exists\" };\r\n            // }\r\n            axios\r\n                .post(\r\n                    \"/updatemembers/\" + id_val_1,\r\n                    {\r\n                        name: name_val_1,\r\n                        email: email_val_1,\r\n                        mobile: mobile_val_1,\r\n                    },\r\n                    {\r\n                        headers: {\r\n                            \"Content-Type\": \"application/json\",\r\n                            // Authorization: 'Bearer ' + varToken\r\n                        },\r\n                    }\r\n                )\r\n                .then((response) => {\r\n                    _this.doEdit(false);\r\n                    _this.getMembers();\r\n                    _this.message = \"updated\";\r\n                })\r\n                .catch((error) => {\r\n                    _this.errors = error.response.data.errors;\r\n                    console.log(\"Insert: \" + error.data);\r\n                });\r\n        },\r\n        numberRistrict() {\r\n            [...document.querySelectorAll('input[type=\"number\"]')].forEach(\r\n                function (item) {\r\n                    item.addEventListener(\"keypress\", function (evt) {\r\n                        if (\r\n                            (evt.which != 8 &&\r\n                                evt.which != 0 &&\r\n                                evt.which < 48) ||\r\n                            evt.which > 57\r\n                        ) {\r\n                            evt.preventDefault();\r\n                        }\r\n                    });\r\n                }\r\n            );\r\n        },\r\n        toggle(e) {\r\n            //console.log(screen.width);\r\n            if (screen.width <= 968) {\r\n                let currEle = e.currentTarget;\r\n                let all = [\r\n                    ...document.getElementsByClassName(\"member-list-single\"),\r\n                ];\r\n                if (currEle.classList.contains(\"active\")) {\r\n                    currEle.classList.remove(\"active\");\r\n                    return;\r\n                }\r\n                all.forEach(function (item) {\r\n                    if (item.classList.contains(\"active\")) {\r\n                        item.classList.remove(\"active\");\r\n                    }\r\n                });\r\n\r\n                currEle.classList.add(\"active\");\r\n            }\r\n        },\r\n    },\r\n    computed: {\r\n        reversedMembers() {\r\n            return [...this.members].reverse().sort();\r\n        },\r\n    },\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.support-memebers-title {\r\n    font-family: \"Perpetua\";\r\n    font-style: normal;\r\n    font-weight: 700;\r\n    font-size: 24.4444px;\r\n    line-height: 28px;\r\n    text-align: center;\r\n    color: #333333;\r\n    position: relative;\r\n    padding: 9px 15px 13px;\r\n}\r\n.support-memebers-title > img {\r\n    border: 1px solid #e3e8ee;\r\n    border-radius: 5px;\r\n    padding: 9px 15px 13px;\r\n    position: absolute;\r\n    right: 22%;\r\n    top: 5%;\r\n    cursor: pointer;\r\n}\r\n.overlay-form {\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r\n    height: 100%;\r\n    overflow-y: auto;\r\n    background-color: rgba(0, 0, 0, 0.4);\r\n    z-index: 999;\r\n}